@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
.ks-console {
  background: black;
  color: #00FF00;
  font-family: "Roboto Mono", monospace;
  width: 100%;
  height: 100%; }
  .ks-console__previous-line {
    margin-left: 20px;
    line-height: 18px;
    white-space: pre; }
  .ks-console__input {
    width: 90%;
    color: #00FF00;
    display: inline-block;
    border: none;
    background: transparent;
    padding-left: 15px; }

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none; }

body {
  height: 100vh; }

#root {
  height: 100%; }

